import styled from 'styled-components';

const StyledButton = styled.button`
  width: 100%;
  padding: 12px;
  border-radius: $m-border-radius / 2;
  font-size: $f-xs;
  font-weight: bold;

  &--submit {
    background: $c-blue;
    color: $c-white;
  }

  &:disabled {
    cursor: not-allowed;
  }

  &--disabled {
    color: $c-disabled-txt;
    background: $c-disabled-bg;
  }
`;

const Section = props => {
  return (
    <StyledSection>
      {props.withContainer ? (
        <StyledContainer>
          {props.children}
        </StyledContainer>
      ) : (
        <div>
          {props.children}
        </div>
      )}
    </StyledSection>
  );
}

export default Section;
